___CSS_LOADER_EXPORT___.push([module.id, `* body {
    box-sizing: border-box;
  }
  
  body {
    margin: 0;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  p {
    margin: 0;
  }
  
  .container {
    width: 500px;
    border: 1px solid #eee;
    border-radius: 3px;
    padding: 20px;
    display: flex;
    flex-direction: column;
  }
  
  form {
    display: flex;
    margin-bottom: 20px;
  }
  
  button {
    padding: 5px 15px;
    border: 0px;
    border-radius: 5px;
    cursor: pointer;
    margin: 0 3px;
  }
  
  input {
    padding: 8px 15px;
    outline: 0;
    border: 1px solid #ddd;
    border-radius: 3px;
  }
  
  form input {
    flex: 1;
    margin-right: 15px;
  }
  
  ul {
    padding: 0;
    list-style: none;
  }
  
  li {
    display: flex;
    align-items: center;
    padding: 10px 0;
  }
  
  li p {
    flex: 1;
  }
  
  li .todo {
    flex: 0 0 20px;
    height: 20px;
    border-radius: 30px;
    margin-right: 15px;
    border: 2px solid #333;
  }
  
  li .todo.done {
    background: #333;
  }`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;EACxB;;EAEA;IACE,SAAS;IACT,iBAAiB;IACjB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;EACzB;;EAEA;IACE,SAAS;EACX;;EAEA;IACE,YAAY;IACZ,sBAAsB;IACtB,kBAAkB;IAClB,aAAa;IACb,aAAa;IACb,sBAAsB;EACxB;;EAEA;IACE,aAAa;IACb,mBAAmB;EACrB;;EAEA;IACE,iBAAiB;IACjB,WAAW;IACX,kBAAkB;IAClB,eAAe;IACf,aAAa;EACf;;EAEA;IACE,iBAAiB;IACjB,UAAU;IACV,sBAAsB;IACtB,kBAAkB;EACpB;;EAEA;IACE,OAAO;IACP,kBAAkB;EACpB;;EAEA;IACE,UAAU;IACV,gBAAgB;EAClB;;EAEA;IACE,aAAa;IACb,mBAAmB;IACnB,eAAe;EACjB;;EAEA;IACE,OAAO;EACT;;EAEA;IACE,cAAc;IACd,YAAY;IACZ,mBAAmB;IACnB,kBAAkB;IAClB,sBAAsB;EACxB;;EAEA;IACE,gBAAgB;EAClB","sourcesContent":["* body {\n    box-sizing: border-box;\n  }\n  \n  body {\n    margin: 0;\n    min-height: 100vh;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n  }\n  \n  p {\n    margin: 0;\n  }\n  \n  .container {\n    width: 500px;\n    border: 1px solid #eee;\n    border-radius: 3px;\n    padding: 20px;\n    display: flex;\n    flex-direction: column;\n  }\n  \n  form {\n    display: flex;\n    margin-bottom: 20px;\n  }\n  \n  button {\n    padding: 5px 15px;\n    border: 0px;\n    border-radius: 5px;\n    cursor: pointer;\n    margin: 0 3px;\n  }\n  \n  input {\n    padding: 8px 15px;\n    outline: 0;\n    border: 1px solid #ddd;\n    border-radius: 3px;\n  }\n  \n  form input {\n    flex: 1;\n    margin-right: 15px;\n  }\n  \n  ul {\n    padding: 0;\n    list-style: none;\n  }\n  \n  li {\n    display: flex;\n    align-items: center;\n    padding: 10px 0;\n  }\n  \n  li p {\n    flex: 1;\n  }\n  \n  li .todo {\n    flex: 0 0 20px;\n    height: 20px;\n    border-radius: 30px;\n    margin-right: 15px;\n    border: 2px solid #333;\n  }\n  \n  li .todo.done {\n    background: #333;\n  }"],"sourceRoot":""}]);
